// localStorageÏóêÏÑú Î©îÏãúÏßÄ Ìò∏Ï∂ú
const storage = {
	fetch(){
		const arr = []

		if(localStorage.length > 0 ){
			for(let i = 0; i < localStorage.length; i++){
				// local webpack Ï†ïÎ≥¥ Îì§Ïñ¥Í∞ÄÏßÄ ÏïäÍ≤å
				if(localStorage.key(i) !== 'loglevel:webpack-dev-server'){
					arr.push(JSON.parse(localStorage.getItem(localStorage.key(i))))
				}
			}
		}
		return arr
	}
}

export default {
	namespaced : true,
	state : {
		todoItems : storage.fetch(),
	},
	getters : {
		storedTodoItems(state){
			return state.todoItems
		}
	},
	mutations: {
    // ÏÉàÎ°úÏö¥ Ìï†Ïùº Ï∂îÍ∞Ä
    addTodoItem(state, newTodoItem){
			let overlapChk = false

			// Î©îÎ™® ÌÖçÏä§Ìä∏ Ï§ëÎ≥µ Ï≤¥ÌÅ¨
			state.todoItems.map(todo => {
				if(todo['item'] === newTodoItem){
					overlapChk = true
				}
			})

			if(overlapChk){
				alert('Ï§ëÎ≥µÎêú Î©îÎ™® ÏûÖÎãàÎã§!')
				return
			} else{
				const obj = {
					completed : false,
					item : newTodoItem
				}
				state.todoItems.push(obj)
				localStorage.setItem(newTodoItem, JSON.stringify(obj))
			}
    },
		// Ìï†Ïùº ÏÇ≠Ï†ú
		removeOneItem(state, payload){
			const {todo, index} = payload
			state.todoItems.splice(index, 1)
			localStorage.removeItem(todo.item)
		},
		// Ìï†Ïùº Ï≤¥ÌÅ¨
		toggleOneItem(state, payload){
			const {todo, index} = payload
			state.todoItems[index].completed = !state.todoItems[index].completed

			// Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄ Í∞±Ïã† (Í∏∞Ï°¥ KeyÍ∞í Ï°¥Ïû¨Ïãú Ìï¥Îãπ keyÍ∞í update)
			localStorage.setItem(todo.item, JSON.stringify(todo))
		},
		// Ï†ÑÏ≤¥ ÏÇ≠Ï†ú
		clearAll(state){
			state.todoItems = []
			localStorage.clear()
		}
	}
}